@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 .625em;
    border-radius: .3125em;
    height: 2.375em;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: .9375em;
        line-height: 120%;
    }
    svg{
        width: 1.3125em;
        height: auto;
        flex-shrink: 0;
        margin-right: .75em;
    }
}

.m-btn-primary{
    color: #fff;
    position: relative;
    font-weight: 700;
    &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border-radius: inherit;
        background: $gradient;
        transition: inherit;
    }
    &:hover{
        color: #fff;
        background-size: 200% auto;
        background-color: darken($primary,10%);
        &:before{
            opacity: 0;
        }
    }
    span{
        position: relative;
        z-index: 2;
    }
    &._black{
        background: #000;
        &:before{
            display: none;
        }
        &:hover{
            background: $primary;
        }
    }
}

.m-btn-outline{
    border: 1px solid rgba($black, 0.44);
    color: $black;
    &:hover{
        background-color: $primary;
        border-color: $primary;
        color: #fff;
        path{
            fill: #fff;
        }
    }
}


@media screen and (max-width: 576px){
    .m-btn{
        height: 2.1875em;
        span{
            font-size: .75em;
        }
    }
}
