.benefit{
  padding: 3.87em 0;
  &__items{
    margin-top: 2.14em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 5em / 3));
    gap: 2.15em 2.5em;
  }
}

.benefit-info{
  @include grid100gap(1.42em);
  max-width: 46em;
}

._card{
  background: #FFFFFF;
  box-shadow: 0 .6875em 3.4375em rgba(0, 0, 0, 0.12);
  border-radius: .6875em;
  padding: 1.25em;
}

.benefit-card{
  display: grid;
  grid-template-columns: 1.375em calc(100% - 2em);
  align-items: flex-start;
  gap: .625em;
  background: $primary;
  color: #fff;
  padding-right: 1em;
  &__icon{
    img{
      max-width: 100%;
      display: block;
    }
  }
  &__content{
    @include grid100gap(.625em);
  }
  &__title{
    line-height: 120%;
  }
}

.faq{
  padding: 3.8em 0;
  background-color: rgba(#eee,.5);
  &__items{
    margin-top: 2.1875em;
    @include grid100gap(1.25em);
  }
}

.faq-card{
  &._active{
    .qa-card__btn{
      span{
        transform: rotate(-45deg);
      }
    }
  }
  &__show{
    display: flex;
    align-items: center;
    justify-content: space-between;
    &:hover{
      cursor: pointer;
    }
  }
  &__btn{
    width: 2.0625em;
    height: 2.75em;
    @include center;
    font-weight: 700;
    span{
      font-size: 1.25em;
      line-height: 125%;
      transition: all .3s;
    }
    &:hover{
      color: $primary;
    }
  }
  &__hidden{
    display: none;
    padding-top: .625em;
  }
}

.new-question{
  display: flex;
  align-items: center;
  margin-top: 2.1875em;
  &__icon{
    width: 2em;
    flex-shrink: 0;
    margin-right: .9375em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__btn{
    margin-left: auto;
    flex-shrink: 0;
    height: 3.5em;
    padding: 0 2.16em;
    span{
      font-size: 1.125em;
    }
  }
}

.feedback,
.blog{
  overflow: hidden;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__nav{
    display: flex;
    align-items: center;
  }
  &__slider{
    margin-top: 2.5em;
  }
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
    }
    &-button{
      position: static;
      margin: 0 0 0 .625em;
      background: #FFFFFF;
      box-shadow: 0 .6875em 3.4375em rgba(0, 0, 0, 0.12);
      border-radius: .3125em;
      width: 3.75em;
      height: 2.8125em;
      svg{
        width: 1.875em;
        height: auto;
      }
      &:hover{
        background: $primary;
        path{
          fill: #fff;
        }
      }
    }
  }
}

.blog{
  padding: 4.0625em 0 5.75em;
  .swiper{
    &-container{
      width: calc(100% + 2.5em);
    }
    &-slide{
      width: calc(100% / 3);
      padding-right: 2.5em;
    }
  }
  &__bottom{
    display: flex;
    margin-top: 2.125em;
  }
  &__btn{
    width: 22em;
    max-width: 100%;
    height: 3.5em;
    span{
      font-size: 1.25em;
    }
  }
}

.feedback{
  padding: 3.87em 0 6.5em;
  .swiper{
    &-slide{
      width: 16.25em;
      padding-right: 1.25em;
    }
  }
  &__text{
    margin-top: 3em;
    color: rgba($dark,.9);
    p,li{
      font-size: 1.125em;
      line-height: 130.5%;
      @include lastM(0 0 .5em);
    }
    ul{
      padding-left: .67em;
      li{
        padding-left: .67em;
        position: relative;
        &:before{
          content: '';
          position: absolute;
          left: 0;
          top: .35em;
          width: .17em;
          height: .17em;
          background-color: rgba($dark,.9);
          border-radius: 50%;
        }
      }
    }
  }
}

.feedback-card{
  &__img{
    @include imgCover(195.8%);
    img{
      border-radius: 1.375em;
      display: block;
    }
  }
}

.news-card{
  height: 100%;
  display: flex;
  flex-direction: column;

  &__img{
    display: block;
    @include imgCover(73%);
    img{
      border-radius: .375em .375em 0 0;
    }
  }
  &__content{
    border-radius: 0 0 .375em .375em;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
  }
  &__info{
    @include grid100gap(.875em);
  }
  &__title{
    line-height: 125%;
  }
  &__desc{
    line-height: 130%;
  }
  &__nav{
    padding-top: .875em;
    margin-top: auto;
    display: flex;
  }
}

.pay-other{
  padding: 4.65em 0 4.325em;
  color: #fff;
  &__inner{
    padding: 3.25em 2.5em;
    box-shadow: 0 .6875em 3.4375em 0 rgba(0, 0, 0, 0.12);
    border-radius: 0.6875em;
    position: relative;
  }
  &__content{
    max-width: 23em;
    position: relative;
    z-index: 2;
    @include grid100gap(1em);
  }
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    img{
      width: 100%;
      height: 100%;
      border-radius: .6875em;
      object-fit: cover;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
  &__nav{
    display: flex;
  }
  &__btn{
    padding: 0 2.53em;
    height: 4.25em;
    span{
      font-size: 1.25em;
    }
  }
}

.about{
  padding: 2.8em 0;
  &__text{
    max-width: 60em;
  }
}

.main{
  border-bottom: 1px solid rgba(#000,.1);
  &__inner{
    position: relative;
    padding: 4.19em 0 5em;
    min-height: 32em;
  }
  &__content{
    max-width: 34.6875em;
  }
  &__img{
    position: absolute;
    top: 1.75em;
    right: 0;
    width: 35em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.prices{
  margin-top: 1.56em;
  &__grid{
    width: 29.5em;
    max-width: 100%;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 1.875em / 4));
    gap: .625em;
    margin-top: .75em;
  }
}

.price-item{
  border-radius: .6875em;
  border: 1px solid rgba(0, 0, 0, 0.55);
  height: 4.25em;
  @include center;
  padding: 0 1em;
  color: rgba(34, 34, 34, 0.55);
  span{
    font-size: 1.25em;
    line-height: 120%;
  }
}

.catalog{
  padding: 4em 0 2em;
  overflow: hidden;
}

.catalog-slider{
  margin-top: .7em;
  @media screen and (min-width: 993px){
    .swiper{
      &-slide{
        width: 100% !important;
      }
      &-wrapper{
        transform: unset !important;
        display: block !important;
      }
    }
  }
  &__nav{
    display: none;
  }
}

.catalog-card{
  padding: 1.25em 0;
  display: grid;
  grid-template-columns: 12.5em calc(100% - 30em) 10em;
  gap: 3.75em;
  align-items: center;
  border-bottom: 1px solid rgba(#000,.1);
  &__img{
    @include imgCover(66.5%);
    img{
      border-radius: 0.3125em;
    }
  }
  &__content{
    @include grid100gap(.875em);
    max-width: 20.4375em;
  }
  &__prices{
    display: flex;
    justify-content: space-between;
    font-weight: 700;
  }
  &__price2{
    opacity: .5;
  }
  &__btn{
    margin-top: .625em;
    width: 100%;
    height: 3.5em;
    text-transform: uppercase;
    span{
      font-size: 1.25em;
    }
  }
}


@media screen and (max-width: 992px){
  .benefit{
    padding: 2.5em 0 2em;
    &__items{
      grid-template-columns: repeat(2,calc(50% - .4em));
      gap: .8em;
      margin-top: 2em;
    }
  }

  .benefit-info{
    gap: .625em;
    .text45{
      max-width: 11em;
    }
  }

  .benefit-card{
    &__title{
      font-size: 1em;
    }
  }

  .faq{
    padding: 2.125em 0 2.25em;
    &__items{
      gap: .9375em;
      margin-top: 1.625em;
    }
  }

  .faq-card{
    &__title{
      font-size: 1em;
      line-height: 130%;
    }
  }

  .blog,
  .feedback{
    .swiper{
      &-button{
        width: 2.87738em;
        height: 2.158em;
        border-radius: 0.23975em;
        svg{
          width: 1.43869em;
        }
      }
    }
  }

  .blog{
    padding: 2.25em 0 4.5em;
    .text45{
      font-size: 1.5625em;
    }
    &__slider{
      margin-top: 1.375em;
    }
    .swiper{
      &-container{
        width: 100%;
      }
      &-slide{
        width: 20em;
        padding-right: 1.25em;
      }
    }
    &__bottom{
      margin-top: 1.25em;
    }
  }

  .feedback{
    padding: 2em 0 3.6em;
    &__slider{
      margin-top: .75em;
    }
    .swiper{
      &-slide{
        width: 8.75em;
        padding-right: .625em;
      }
    }
    &__text{
      margin-top: 2em;
      p,li{
        font-size: .9375em;
      }
    }
  }

  .feedback-card{
    &__img{
      img{
        border-radius: .76em;
      }
    }
  }

  .news-card{
    &__img{
      padding-bottom: 80%;
    }
  }

  .pay-other{
    padding: 4.65em 0 2.12em;
    &__inner{
      padding: 1.25em;
    }
    &__content{
      gap: .625em;
    }
    &__btn{
      padding: 0 1.5em;
    }
  }

  .main{
    border-bottom: none;
    &__inner{
      padding: 1.38em 0 3em;
    }
    &__img{
      position: static;
      max-width: 100%;
      margin-top: .875em;
    }
  }

  .about{
    padding: 1.1875em 0;
    .text45{
      font-size: 1.5625em;
    }
    &__text{
      margin-top: .875em;
    }
  }

  .catalog{
    padding: 3em 0 2em;
  }

  .catalog-slider{
    margin-top: 1.2em;
    .swiper{
      &-slide{
        padding-right: 1.56em;
        width: 15.12em;
      }
      &-button{
        position: static;
        margin: 0 0 0 .48em;
        background: #FFF;
        box-shadow: 0px 8.44022px 42.2011px 0px rgba(0, 0, 0, 0.12);
        width: 2.87738em;
        height: 2.158em;
        border-radius: 0.23975em;
        &:hover{
          background: $primary;
          path{
            fill: #fff;
          }
        }
        svg{
          width: 1.5em;
          height: auto;
        }
      }
    }
    &__nav{
      margin-top: 1.9375em;
      display: flex;
      justify-content: flex-end;
    }
  }

  .catalog-card{
    display: block;
    padding: 0;
    border: none;
    &__content{
      margin: 1.2em 0;
      gap: .5em;
    }
    &__prices{
      justify-content: unset;
    }
    &__price2{
      margin-left: .5em;
    }
    &__btn{
      width: 8.625em;
      height: 3em;
      span{
        font-size: .9375em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .benefit{
    &__items{
      grid-template-columns: 100%;
    }
  }

  .new-question{
    flex-direction: column;
    text-align: center;
    margin-top: 2.75em;
    &__icon{
      margin: 0 0 .9375em;
    }
    &__btn{
      margin: .9375em auto 0;
      height: 2.375em;
      padding: 0 .625em;
      span{
        font-size: .9375em;
      }
    }
  }

  .pay-other{
    &__inner{
      padding-top: 15.94em;
    }
    &__img{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    .text30{
      font-size: 1.25em;
      max-width: 80%;
    }
    &__btn{
      width: 100%;
    }
  }

  .prices{
    .text20{
      font-size: .8395em;
    }
    &__grid{
      margin-top: .53em;
      grid-template-columns: repeat(4,calc(25% - .42em * 3 / 4));
      gap: .42em;
    }
  }

  .price-item{
    height: 2.85em;
    padding: 0 .5em;
    border-radius: 0.46175em;
    span{
      font-size: .8395em;
    }
  }
}
