.modal{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    &__bg {
        position: fixed;
        width: 100%;
        height: calc(100 * var(--vh));
        top: 0;
        left: 0;
        display: none;
        z-index: 2;
        background: #414141;
        backdrop-filter: blur(16.5px);
    }
    &--active {
        opacity: 1;
        pointer-events: unset;
        .product-modal__bg,
        .modal__bg {
            display: block;
        }
    }
    &__close {
        position: absolute;
        top: 1.19em;
        right: 1.63em;
        width: 3.25em;
        height: 3.25em;
        border-radius: 50%;
        background: #FFF;
        box-shadow: 0px 11px 33px 0px rgba(0, 0, 0, 0.25);
        @include center;
        svg {
            width: 1.125em;
            height: auto;
        }
        &:hover{
            background-color: $primary;
            path{
                fill: #fff;
            }
        }
    }
    &__dialog{
        position: relative;
        z-index: 3;
        width: 60em;
        max-width: calc(100% - 2.5em);
        min-height: 60em;
        border-radius: 0.3125em;
        background: #FFF;
        max-height: calc(100 * var(--vh));
        overflow-y: auto;
        padding: 3.5em 11.6em;
        @include center;
    }
}

.login-modal{
    &__top{
        text-align: center;
        @include grid100gap(1.5em);
    }
}

.login-form{
    margin-top: 1.75em;
    @include grid100gap(.94em);
    &__btn{
        height: 5.0625em;
        span{
            font-size: 1.5625em;
        }
    }
}

.modal-product{
    margin-top: 1.94em;
    &__wrap{
        margin-top: .44em;
        padding: 1.2em 0;
        border-top: 1px solid rgba(#000,.2);
        border-bottom: 1px solid rgba(#000,.2);
        display: grid;
        grid-template-columns: 6.8125em calc(100% - 9.8125em);
        align-items: center;
        gap: 3em;
    }
    &__img{
        img{
            width: 100%;
            display: block;
            border-radius: 0.3125em;
        }
    }
    &__content{
        @include grid100gap(.69em);
    }
}



@media screen and (max-width: 992px){
    .modal{
        padding: 1.25em;
        display: block;
        overflow-y: auto;
        &__dialog{
            max-width: 100%;
            padding: 1.875em 1.25em;
            min-height: 32em;
            max-height: unset;
            overflow: unset;
        }
        &__close{
            width: 2.5em;
            height: 2.5em;
            top: 1.5em;
            right: .95em;
            svg{
                width: .875em;
            }
        }
    }

    .login-modal{
        .modal__dialog{
            display: block;
        }
        &__top{
            text-align: left;
            gap: 1.125em;
            .text20{
                b{
                    max-width: 85%;
                    display: block;
                }
                br{
                    display: none;
                }
            }
        }
    }

    .login-form{
        &__btn{
            height: 4em;
            span{
                font-size: 1em;
            }
        }
    }

    .modal-product{
        margin-top: 2em;
        &__wrap{
            grid-template-columns: 6.625em calc(100% - 7.625em);
            gap: 1em;
        }
        &__content{
            max-width: 10em;
            gap: .56em;
        }
    }
}
